___CSS_LOADER_EXPORT___.push([module.id, `.header {
  width: 100%;
  height: 80px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: var(--header-layer);
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  gap: 0;
  flex-wrap: nowrap;
}
@media (max-width: 768px) {
  .header {
    justify-content: space-between;
    padding-left: 20px;
  }
}
.header__links {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 24px;
  flex-wrap: nowrap;
}
.header__link {
  display: inline-block;
  position: relative;
  overflow-x: hidden;
  color: var(--white);
  font-size: 16px;
  line-height: 28px;
}
.header__link::before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--white);
  position: absolute;
  left: 0;
  bottom: 2px;
  transition: translate var(--hover-transition);
  translate: -100%;
}
.header__link:hover::before {
  translate: 0;
