___CSS_LOADER_EXPORT___.push([module.id, `.accordion {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 0;
  flex-wrap: nowrap;
}
.accordion__item {
  width: 100%;
  border-top: 1px solid var(--extralight-gray);
  padding: 24px 0;
}
.accordion__item:last-of-type {
  padding-bottom: 0;
}
.accordion__item_active .accordion__status-icon {
  rotate: 45deg;
}
.accordion__item_active .accordion__content {
  padding-top: 24px;
  grid-template-rows: 1fr;
}
.accordion__heading {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  gap: 20px;
  flex-wrap: nowrap;
  cursor: pointer;
  will-change: color;
  transition: color var(--hover-transition);
}
.accordion__heading:hover {
  color: var(--color-primary);
}
.accordion__title {
  font-size: 18px;
  line-height: 32px;
  font-weight: 600;
}
.accordion__status-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 0;
  flex-wrap: nowrap;
  width: 24px;
  aspect-ratio: 1/1;
  font-size: 20px;
  border: 2px solid;
  border-radius: 100%;
  will-change: rotate;
  transition: rotate 0.5s ease-in-out;
}
.accordion__status-icon i {
  width: 20px;
  height: 20px;
}
.accordion__content {
  display: grid;
  grid-template-rows: 0fr;
  will-change: grid-template-rows, padding-top;
  transition: grid-template-rows 0.5s ease-in-out, padding-top 0.5s ease-in-out;
  transition: grid-template-rows 0.5s ease-in-out, padding-top 0.5s ease-in-out, -ms-grid-rows 0.5s ease-in-out;
}
.accordion__content-inner {
  overflow: hidden;
  font-size: 18px;
  line-height: 32px;
  color: var(--dark-gray);
  padding-right: 52px;
}
@media (max-width: 375px) {
  .accordion__content-inner {
    padding-right: 44px;
  }
