___CSS_LOADER_EXPORT___.push([module.id, `.third-block {
  padding-top: 80px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 32px;
  flex-wrap: nowrap;
}
.third-block__heading {
  font-size: 32px;
  line-height: 40px;
  font-weight: 600;
}
@media (max-width: 1268px) {
  .third-block__heading {
    font-size: 20px;
    line-height: 24px;
    font-weight: 600;
  }
}
.third-block__text {
  font-size: 16px;
  line-height: 28px;
  width: 592px;
}
@media (max-width: 1268px) {
  .third-block__text {
    width: max(347px, min(30.8333333333vw, 592px));
  }
}
.third-block__description {
  padding-top: 18px;
  color: var(--dark-gray);
}
.third-block__images {
  position: relative;
  width: 592px;
  aspect-ratio: 592/448;
}
@media (max-width: 1268px) {
  .third-block__images {
    width: max(351px, min(30.8333333333vw, 592px));
  }
}
.third-block__truck-image, .third-block__man-image {
  width: 100%;
  height: 100%;
}
.third-block__truck-image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
@media (max-width: 768px) {
  .third-block {
    padding-top: 56px;
  }
}
@media (max-width: 767px) {
  .third-block {
    flex-direction: column-reverse;
    text-align: center;
  }
}
@media (max-width: 375px) {
  .third-block {
    width: 100%;
  }
  .third-block__text, .third-block__images {
    width: 100%;
  }
