___CSS_LOADER_EXPORT___.push([module.id, `.slider {
  margin: 0 auto;
  width: 100%;
  --slide-gap: 80px;
}
.slider__inner {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 0;
  flex-wrap: nowrap;
}
.slider__viewport {
  width: 100%;
  overflow-x: hidden;
  padding: 32px 0;
}
.slider__slides {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  gap: var(--slide-gap);
  flex-wrap: nowrap;
  transition: translate 0.5s ease-in-out;
}
.slider__slide {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 32px;
  flex-wrap: nowrap;
}
.slider__pagination {
  padding-top: 18px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 17px;
  flex-wrap: nowrap;
}
@media (max-width: 768px) {
  .slider__pagination {
    padding-top: 4px;
  }
}
.slider__pagination-button {
  width: 8px;
  aspect-ratio: 1/1;
  border-radius: 100%;
  border: none;
  padding: 0;
}
.slider__pagination-button_active {
  pointer-events: none;
  background-color: var(--color-primary);
}
.slider__pagination-button:hover {
  background-color: var(--color-primary-hover);
