  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
  gap: 9px;
  flex-wrap: nowrap;
}
.logo__circle {
  display: inline-block;
  width: 24px;
  aspect-ratio: 1/1;
  border-radius: 100%;
}
.logo__circle:first-of-type {
  background-color: var(--color-primary);
  translate: 50%;
}
.logo__circle:last-of-type {
  background-color: var(--light-gray);
}
.logo__heading {
  font-size: 20px;
  line-height: 24px;
  font-weight: 600;
}
.logo_light .logo__heading {
  color: var(--white);
}
.logo_dark .logo__heading {
  color: var(--black);
