  .hero-section::before {
    background-size: auto 100%;
  }
}
@media (max-width: 374px) {
  .hero-section::before {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  }
}
.hero-section::after {
  background-color: var(--color-primary);
  opacity: 0.8;
  z-index: -1;
}
