___CSS_LOADER_EXPORT___.push([module.id, `.burger-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--mobile-navigation-bg-layer);
  width: 100%;
  height: 100vh;
  pointer-events: none;
}
.burger-menu_open {
  pointer-events: all;
}
.burger-menu_open .burger-menu__background {
  opacity: 1;
}
.burger-menu_open .burger-menu__inner {
  translate: 0;
}
.burger-menu__background {
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  will-change: opacity;
  transition: opacity ease-in-out 400ms;
  width: 100%;
  height: 100%;
}
.burger-menu__inner {
  position: absolute;
  top: 0;
  right: 0;
  translate: 100%;
  will-change: translate;
  transition: translate ease-in-out 400ms;
  width: min(100%, 375px);
  height: 100%;
  overflow: hidden;
  background-color: var(--white);
}`, "",{"version":3,"sources":["webpack://./src/components/shared/BurgerMenu/BurgerMenu.scss"],"names":[],"mappings":"AAAA;EACE,eAAA;EACA,MAAA;EACA,OAAA;EACA,0CAAA;EAEA,WAAA;EACA,aAAA;EACA,oBAAA;AAAF;AAME;EACE,mBAAA;AAJJ;AAMI;EACE,UAAA;AAJN;AAOI;EACE,YAAA;AALN;AASE;EACE,kBAAA;EACA,MAAA;EACA,OAAA;EAEA,oCAAA;EAEA,UAAA;EACA,oBAAA;EACA,qCAAA;EAEA,WAAA;EACA,YAAA;AAVJ;AAaE;EACE,kBAAA;EACA,MAAA;EACA,QAAA;EAEA,eAAA;EACA,sBAAA;EACA,uCAAA;EAEA,uBAAA;EACA,YAAA;EACA,gBAAA;EAEA,8BAAA;AAdJ","sourcesContent":[".burger-menu {\n  position: fixed;\n  top: 0;\n  left: 0;\n  z-index: var(--mobile-navigation-bg-layer);\n\n  width: 100%;\n  height: 100vh;\n  pointer-events: none;\n\n  $state-transition: ease-in-out 400ms;\n\n  $self: &;\n\n  &_open {\n    pointer-events: all;\n\n    #{$self}__background {\n      opacity: 1;\n    }\n\n    #{$self}__inner {\n      translate: 0;\n    }\n  }\n\n  &__background {\n    position: absolute;\n    top: 0;\n    left: 0;\n\n    background-color: rgba(0, 0, 0, 0.5);\n\n    opacity: 0;\n    will-change: opacity;\n    transition: opacity $state-transition;\n\n    width: 100%;\n    height: 100%;\n  }\n\n  &__inner {\n    position: absolute;\n    top: 0;\n    right: 0;\n\n    translate: 100%;\n    will-change: translate;\n    transition: translate $state-transition;\n\n    width: min(100%, 375px);\n    height: 100%;\n    overflow: hidden;\n\n    background-color: var(--white);\n  }\n}\n"],"sourceRoot":""}]);
