  background-color: var(--extralight-gray);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
  gap: 32px 36px;
  flex-wrap: wrap;
}
.text-section {
  padding: 68px 20px;
}
@media (max-width: 768px) {
  .text-section {
    padding: 20px 20px 32px;
  }
}
@media (max-width: 375px) {
  .text-section {
    padding: 20px 16px;
  }
}
@media (max-width: 320px) {
  .text-section {
    padding: 16px;
  }
}
.text-section__heading {
  font-size: 40px;
  line-height: 56px;
  font-weight: 600;
  text-align: center;
}
@media (max-width: 375px) {
  .text-section__heading {
    font-size: 24px;
    line-height: 32px;
    font-weight: 600;
  }
}
.text-section__text-block {
  width: 592px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  gap: 16px;
  flex-wrap: nowrap;
}
@media (max-width: 1260px) and (min-width: 768px) {
  .text-section__text-block {
    gap: 48px;
  }
}
@media (min-width: 1260px) {
  .text-section__text-block {
    gap: 16px;
  }
}
.text-section__title {
  font-size: 18px;
  line-height: 32px;
  font-weight: 600;
}
.text-section__description {
  font-size: 16px;
  line-height: 28px;
