___CSS_LOADER_EXPORT___.push([module.id, `.text-input__label {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 0;
  flex-wrap: nowrap;
}
.text-input_dirty .text-input__label-text, .text-input:has(input[placeholder], input:focus) .text-input__label-text {
  translate: 0 -12px;
  font-size: 14px;
}
.text-input_dirty .text-input__field, .text-input:has(input[placeholder], input:focus) .text-input__field {
  padding-top: 28px;
  padding-bottom: 4px;
}
.text-input__label-text {
  font-size: 18px;
  line-height: 32px;
  color: var(--dark-gray);
  pointer-events: none;
  position: absolute;
  left: 16px;
  will-change: font-size, translate;
  transition: font-size ease-in-out 0.25s, translate ease-in-out 0.25s;
}
.text-input__field {
  width: 100%;
  padding: 16px;
  outline: none;
  border: 2px solid var(--extralight-gray);
  font-size: 18px;
  line-height: 32px;
  background-color: var(--extralight-gray);
  will-change: background-color, border-color, padding-top, padding-bottom;
  transition: background-color ease-in-out 0.25s, border-color ease-in-out 0.25s, padding-top ease-in-out 0.25s, padding-bottom ease-in-out 0.25s;
}
.text-input__field:focus {
  background-color: var(--white);
}
.text-input__validity-icon {
  z-index: 1;
  font-size: 20px;
  height: 20px;
  position: absolute;
  right: 16px;
}
.text-input_dirty.text-input_invalid .text-input__field, .text-input_touched.text-input_invalid .text-input__field {
  border-color: var(--color-dangerous);
}
.text-input_dirty.text-input_invalid .text-input__helper-text,
.text-input_dirty.text-input_invalid .text-input__validity-icon, .text-input_touched.text-input_invalid .text-input__helper-text,
.text-input_touched.text-input_invalid .text-input__validity-icon {
  color: var(--color-dangerous);
}
.text-input_dirty.text-input_valid .text-input__validity-icon, .text-input_touched.text-input_valid .text-input__validity-icon {
  color: var(--color-success);
}
.text-input__helper-text {
  padding-top: 4px;
  font-size: 12px;
  line-height: 16px;
  color: var(--gray);
  will-change: color, opacity;
  transition: color ease-in-out 0.25s;
  animation: smooth-appear ease-in-out 0.25s forwards;
