}\n}\n","@mixin font-bold {\n  font-weight: 600;\n}\n\n@mixin heading-1 {\n  font-size: 52px;\n  line-height: 64px;\n  @include font-bold;\n}\n\n@mixin heading-2 {\n  font-size: 40px;\n  line-height: 56px;\n  @include font-bold;\n}\n\n@mixin heading-3 {\n  font-size: 32px;\n  line-height: 40px;\n  @include font-bold;\n}\n\n@mixin heading-4 {\n  font-size: 24px;\n  line-height: 32px;\n  @include font-bold;\n}\n\n@mixin heading-5 {\n  font-size: 20px;\n  line-height: 24px;\n  @include font-bold;\n}\n\n@mixin heading-6 {\n  font-size: 16px;\n  line-height: 24px;\n  @include font-bold;\n}\n\n@mixin font-18 {\n  font-size: 18px;\n  line-height: 32px;\n}\n\n@mixin font-16 {\n  font-size: 16px;\n  line-height: 28px;\n}\n\n@mixin font-14 {\n  font-size: 14px;\n  line-height: 24px;\n}\n\n@mixin font-12 {\n  font-size: 12px;\n  line-height: 16px;\n}\n"],"sourceRoot":""}]);
